import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { IPaginacion } from '../../interfaces/paginacion.interface';
import { SearchPagination } from '../../interfaces/search.pagination';
import { PeriodoAcademicoModel } from '../../models/aerc-models/periodo-academico-model';
import { SolicitudModel } from '../../models/aerc-models/solicitud-model';
import { TipoSolicitudModel } from '../../models/aerc-models/tipo-solicitud-model';
import { AprobadorAercModel } from '../../models/aerc-models/aprobador-aerc.model';
import { EscuelaAercModel } from '../../models/aerc-models/escuela-aerc.model';
import { FacultadAercModel } from '../../models/aerc-models/facultad-aerc.model';
import { TipoAprobadorAercModel } from '../../models/aerc-models/tipo-aprobador-aerc.model';

@Injectable({
  providedIn: 'root'
})
export class EscuelaFacultadAercService {


  private urlBase = `${environment.backend.aerc}api/escfac`;
  constructor(
    private http: HttpClient,
  ) { }

  public listarEscuelas(): Observable<Array<EscuelaAercModel>> {
    return this.http.get<Array<EscuelaAercModel>>(`${this.urlBase}/find/escuela`);
  }

  public listarFacultades(): Observable<Array<FacultadAercModel>> {
    return this.http.get<Array<FacultadAercModel>>(`${this.urlBase}/find/facultad`);
  }

  public listarTipoAprobador(): Observable<Array<TipoAprobadorAercModel>> {
    return this.http.get<Array<TipoAprobadorAercModel>>(`${this.urlBase}/find/tipo-aprobador`);
  }

}
