import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Store } from '@ngrx/store';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { Subscription } from 'rxjs';
import { SearchPagination } from 'src/app/commons/interfaces/search.pagination';
import { EscuelaAercModel } from 'src/app/commons/models/aerc-models/escuela-aerc.model';
import { FacultadAercModel } from 'src/app/commons/models/aerc-models/facultad-aerc.model';
import { UtilAlert } from 'src/app/commons/util/util.alert';
import { listarAprobadores } from 'src/app/store/actions/aerc-actions/aprobador.actions';
import { AppState } from 'src/app/store/app.reducer';
import { AprobadorAercModel } from '../../../../commons/models/aerc-models/aprobador-aerc.model';
import { TipoAprobadorAercModel } from '../../../../commons/models/aerc-models/tipo-aprobador-aerc.model';
import { AprobadorAercService } from '../../../../commons/services/aerc-services/aprobador-aerc.service';
import { EscuelaFacultadAercService } from '../../../../commons/services/aerc-services/escuela-facultad.service';
import { AprobadorSignAercModel } from '../../../../commons/models/aerc-models/aprobador-sign.model';

@Component({
  selector: 'app-admin-aprobador',
  templateUrl: './admin-aprobador.component.html',
  styles: [
  ]
})
export class AdminAprobadorComponent implements OnInit {

  public _subscriptionAprobadorState: Subscription;
  public listadoAprobadores: AprobadorAercModel[] = [];

  //*
  public formTopBar: FormGroup;
  public searchPagination: SearchPagination<string> = null;
  public pageSize: number = 10;
  public page: number = 1;
  public totalElements = 0;
  public tipo: number = 0;
  public escuela: string = 'all';
  public facultad: string = 'all';
  public listadoEscuela: Array<EscuelaAercModel> = [];
  public listadoFacultad: Array<FacultadAercModel> = [];
  public listadoTipoAprobador: Array<TipoAprobadorAercModel> = [];
  //*

  private timer = null;
  //*
  public formAprobador: FormGroup;
  public accion: 'new' | 'edit';
  private tempAprobadorId = '';
  //*

  public loading = false;
  @BlockUI('windowAprobadores') blockUI: NgBlockUI;

  public isEdicion: boolean = false;
  public aprobadorTemp = new AprobadorAercModel();


  constructor(
    private store: Store<AppState>,
    private escFacService: EscuelaFacultadAercService,
    private alert: UtilAlert,
    private aprobadorService: AprobadorAercService,

  ) { }

  //* INICIACION DE LA VENTANA
  //* ----------------------------------------------------------------------------------

  ngOnInit(): void {
    this.crearTopBar();
    this.listarAprobadores();
    this._listarEscuelas();
    this._listarFacultades();
    this._listarTipoAprobadores();
    this._aprobadorState();
  }

  ngOnDestroy(): void {
    this._subscriptionAprobadorState?.unsubscribe();
  }

  crearTopBar() {
    this.formTopBar = new FormGroup({
      escuela: new FormControl(""),
      facultad: new FormControl(""),
      tipo: new FormControl(""),
      registros: new FormControl("")
    });

    this.formTopBar.get("escuela").setValue(this.escuela.toString());
    this.formTopBar.get("facultad").setValue(this.facultad.toString());
    this.formTopBar.get("tipo").setValue(this.tipo.toString());
    this.formTopBar.get("registros").setValue(this.pageSize.toString());
  }

  //* STATE REDUX
  //* ----------------------------------------------------------------------------------

  private _aprobadorState() {
    this._subscriptionAprobadorState = this.store.select("aprobadorAerc").subscribe((state) => {
      this.loading = state.loading;

      if (state.searchPagination != null) {
        this.searchPagination = state.searchPagination;
        this.page = state.searchPagination.page;
        this.pageSize = state.searchPagination.records;
        this.escuela = state.escuela;
        this.facultad = state.facultad;
        this.tipo = state.tipo;
        if (this.formTopBar.get("escuela").value != this.escuela.toString()) {
          this.formTopBar.get("escuela").setValue(this.escuela);
          this.page = 1;
        }

        if (this.formTopBar.get("facultad").value != this.facultad.toString()) {
          this.formTopBar.get("facultad").setValue(this.facultad);
          this.page = 1;
        }

        if (this.formTopBar.get("registros").value != this.pageSize.toString()) {
          this.formTopBar.get("registros").setValue(this.pageSize);
        }

        if (this.formTopBar.get("tipo").value != this.tipo.toString()) {
          this.formTopBar.get("tipo").setValue(this.tipo);
          this.page = 1;
        }
      }

      if (state.listadoAprobadoresPaginated) {
        this.listadoAprobadores = state.listadoAprobadoresPaginated.content;
        this.totalElements = state.listadoAprobadoresPaginated.totalElements;
      }

      if (this.blockUI.isActive != state.loading) {
        state.loading ? this.blockUI.start("Buscando aprobadores...") : this.blockUI.stop();
      }
    })
  }



  //* METODOS PARA LISTAR Y CAMBIAR LAS SOLICITUDES
  //* ----------------------------------------------------------------------------------

  listarAprobadores() {
    this.listadoAprobadores = [];
    if (this.searchPagination == null) {
      this.searchPagination = {
        page: this.page,
        records: this.pageSize,
        seek: '',
      }
    } else {
      this.searchPagination = null
      this.listarAprobadores();
    }
    this.store.dispatch(
      listarAprobadores({ searchPagination: this.searchPagination, escuela: this.escuela, facultad: this.facultad, tipo: this.tipo }))
  }

  _listarEscuelas() {
    this.escFacService.listarEscuelas().subscribe(result => {
      if (result) {
        this.listadoEscuela = result;
      }
    })
  }

  _listarFacultades() {
    this.escFacService.listarFacultades().subscribe(result => {
      if (result) {
        this.listadoFacultad = result;
      }
    })
  }

  _listarTipoAprobadores() {
    this.escFacService.listarTipoAprobador().subscribe(result => {
      if (result) {
        this.listadoTipoAprobador = result;
      }
    })
  }

  cambiarTipo(tipoServicio: number) {
    this.tipo = tipoServicio;
    this.listarAprobadores();
  }

  cambiarEscuela(escuela: string) {
    this.escuela = escuela;
    this.listarAprobadores();
  }

  cambiarFacultad(facultad: string) {
    this.facultad = facultad;
    this.listarAprobadores();
  }

  public volver(): void {
    this.isEdicion = false;
    this.tempAprobadorId = '0';
    this.aprobadorTemp = new AprobadorAercModel();
  }

  //** METODOS PARA GUARDAR Y EDITAR APROBADORES */

  public initFormAprobador() {
    this.formAprobador = new FormGroup({
      usuario: new FormControl("", [Validators.required]),
      nombre: new FormControl("", [Validators.required]),
      correo: new FormControl("", [Validators.required]),
      escuela: new FormControl("", [Validators.required]),
      facultad: new FormControl("", [Validators.required]),
      cargo: new FormControl("", [Validators.required])
    })
  }

  public setAprobadorForm(aprobador: AprobadorAercModel) {
    this.formAprobador.controls['usuario'].setValue(aprobador.usuario);
    this.formAprobador.controls['nombre'].setValue(aprobador.nombre);
    this.formAprobador.controls['correo'].setValue(aprobador.correo);
    this.formAprobador.controls['escuela'].setValue(aprobador.escuela.id);
    this.formAprobador.controls['facultad'].setValue(aprobador.facultad.id);
    this.formAprobador.controls['cargo'].setValue(aprobador.tipoAprobador.id.toString());
    this.tempAprobadorId = aprobador.id;
  }

  public newAprobador(accion: 'new' | 'edit', aprobador: AprobadorAercModel) {
    this.isEdicion = true;
    this.accion = accion;
    this.initFormAprobador();
    if (accion == 'edit') {
      this.setAprobadorForm(aprobador);
      this.aprobadorTemp = aprobador;
    }
  }

  public guardarAprobador() {
    if (this.formAprobador.valid) {
      let newAprobador = this.generarAprobador();

      this.blockUI.start("Guardando aprobador...");
      if (this.accion == 'edit') {
        newAprobador.id = this.tempAprobadorId;
        this.aprobadorService.editAprobador(newAprobador).subscribe(result => {
          if (result != null) {
            this.blockUI.stop();
            this.alert.successSwal("Se ha editado el aprobador " + newAprobador.usuario)
          }
        })
      }

      if (this.accion == 'new') {
        this.blockUI.update("Asignando rol...");
        this.aprobadorService.saveNewAprobador(newAprobador).subscribe(result => {
          if (result != null) {
            this.blockUI.stop();
            this.alert.successSwal("Se ha guardado el aprobador " + newAprobador.usuario)
          }
        })
      }
      this.volver();
    } else {
      this.alert.toastError("Debe completar todo el formulario");
    }
  }

  public buscarSign() {
    const usuario = this.formAprobador.controls['usuario'].value;
    if (usuario == null || usuario == '') {
      this.alert.toastError("No ha ingresado el usuario del aprobador");
    }
    else {
      this.blockUI.start("Buscando aprobador...");
      this.aprobadorService.buscarAprobadorSign(usuario).subscribe((result) => {
        if (!result.error) {
          this.formAprobador.controls['usuario'].setValue(result.resultado.usuarioEmail);
          this.formAprobador.controls['nombre'].setValue(result.resultado.nombre);
          let email = result.resultado.usuarioEmail.concat("@uft.cl");
          this.formAprobador.controls['correo'].setValue(email);
        }
        else {
          this.alert.toastError("No se ha encontrado al usuario " + usuario);
        }
        this.blockUI.stop();
      });
    }
  }

  private generarAprobador(): AprobadorAercModel {
    let newAprobador = new AprobadorAercModel();

    newAprobador.nombre = this.formAprobador.controls['nombre'].value;
    newAprobador.usuario = this.formAprobador.controls['usuario'].value;
    newAprobador.correo = this.formAprobador.controls['correo'].value;

    let escuela = this.listadoEscuela.filter(e => e.id == this.formAprobador.controls['escuela'].value)[0];
    let facultad = this.listadoFacultad.filter(e => e.id == this.formAprobador.controls['facultad'].value)[0];
    let cargo = this.listadoTipoAprobador.filter(e => e.id == this.formAprobador.controls['cargo'].value)[0];

    newAprobador.escuela = escuela;
    newAprobador.facultad = facultad;
    newAprobador.tipoAprobador = cargo;

    return newAprobador;
  }

}
